import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { getBuildings, deleteBuilding } from 'services/building';
import { TableData, ColEditDelete } from 'components/common';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__CO_SO_DAO_TAO__TOA_NHA__NHAP_DU_LIEU;

class DormitoryBuilding extends Component {
  state = { reload: 0 };

  columns = [
    {
      title: 'C s o to',
      dataIndex: 'base',
      key: 'base',
      width: 200,
    },
    {
      title: 'M',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Tn',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'S tng',
      dataIndex: 'noOfFloor',
      key: 'noOfFloor',
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        </Fragment>
      ),
    },
  ];

  handleDelete = async id => {
    await deleteBuilding(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    return (
      <Card title="Ta nh">
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              bordered
              columns={this.columns}
              service={getBuildings}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DormitoryBuilding />
  </Page>
);



